<template>
    <div class="ExpView02"></div>
    <div class="btns"></div>
</template>

<script>
import * as THREE from 'three'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'

export default {
  name: 'ExpView02',
  data () {
    return {}
  },
  methods: {
    main () {
      this.canvas = document.querySelector('.ExpView02')
      this.renderer = new THREE.WebGLRenderer({ antialias: true })
      this.renderer.setSize(this.canvas.clientWidth, this.canvas.clientHeight)
      this.renderer.outputEncoding = THREE.sRGBEncoding
      this.canvas.appendChild(this.renderer.domElement)

      this.scene = new THREE.Scene()

      const fov = 45
      const width = this.canvas.clientWidth
      const height = this.canvas.clientHeight
      const near = 100
      const far = 1000
      this.camera = new THREE.PerspectiveCamera(fov, width / height, near, far)
      this.camera.position.set(0, 0, 300)
      this.scene.add(this.camera)

      this.control = new OrbitControls(this.camera, this.renderer.domElement)

      const loader = new GLTFLoader()
      loader.load('data/glb/timmy.glb', (gltf) => {
        const model = gltf.scene
        this.scene.add(model)

        this.animations(gltf)
      })

      const plane = new THREE.Mesh(
        new THREE.PlaneGeometry(500, 500),
        new THREE.MeshBasicMaterial({
          color: 0xffffff,
          side: THREE.DoubleSide
        })
      )
      this.scene.add(plane)
      plane.rotation.x = Math.PI * 0.5
      plane.position.y = -100

      const grid = new THREE.GridHelper(500, 20)
      this.scene.add(grid)
      grid.position.y = -100

      this.light = new THREE.DirectionalLight({ color: 0x000000, intensity: 1 })
      this.light.position.set(-1, 2, 4)
      this.camera.add(this.light)
    },
    render (time) {
      this.update(time)
      this.renderer.render(this.scene, this.camera)
      requestAnimationFrame(this.render)
      this.control.update()
    },
    resize () {
      const canvas = document.querySelector('.ExpView02')
      const width = canvas.clientWidth
      const height = canvas.clientHeight
      this.renderer.setSize(width, height)
      this.camera.aspect = width / height
      this.camera.updateProjectionMatrix()
    },
    update (time) {
      time *= 0.001

      if (this.mixer) {
        const xTime = time - this.previousTime
        this.mixer.update(xTime)
      }
      this.previousTime = time
    },
    changeAnimation (animationName) {
      const previousAcition = this.currentAction
      this.currentAction = this.animationMap[animationName]

      if (previousAcition !== this.currentAction) {
        previousAcition.fadeOut(0.5)
        this.currentAction.reset().fadeIn(0.5).play()
      }
    },
    animations (gltf) {
      const model = gltf.scene
      const gltfAnimation = gltf.animations

      this.mixer = new THREE.AnimationMixer(model)
      this.animationMap = {}

      const btnsDIV = document.querySelector('.btns')
      gltfAnimation.forEach(animationClip => {
        const name = animationClip.name
        const btns = document.createElement('div')
        btns.classList.add('on')
        btns.innerText = name
        btnsDIV.appendChild(btns)

        const animationAction = this.mixer.clipAction(animationClip)
        this.animationMap[name] = animationAction

        btns.addEventListener('click', () => {
          this.animationName = btns.innerHTML
          this.changeAnimation(this.animationName)
        })
      })

      this.currentAction = this.animationMap.Idle
      this.currentAction.play()
    }
  },
  mounted () {
    this.main()
    this.render()
  }
}
</script>

<style scoped lang="scss">
.ExpView02{
  width:100%;
  height: 100%;
}
.btns{
  display: flex;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 3vh;
}
.on{
  padding: 10px;
  margin: 10px;
  border-radius: 8px;
  background-color: rgba(0,0,0,0.5);
  cursor: pointer;
  border: 1px solid #000;
}
</style>
